/* default */
html {
  font-size: 62.5%; }

html, body {
  width: 100%;
  height: 100%; }

body {
  font-family: 'notokr', sans-serif;
  -webkit-font-smoothing: antialiased;
  -webkit-text-size-adjust: none;
  -moz-osx-font-smoothing: grayscale; }

body:before {
  content: '';
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 0; }

/* fix webkit bug(scroll event delay) */
body, div, h1, h2, h3, h4, h5, h6, form, fieldset, p, button, iframe, tr, tbody, thead, table, th, td, b, span, strong {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

/* layout */
body {
  font-size: 1.4rem;
  letter-spacing: -0.05em;
  line-height: 1.9rem;
  font-weight: 300; }

/* common */
button {
  border: 0;
  font-family: 'notokr';
  cursor: pointer;
  background: transparent;
  box-sizing: border-box;
  color: #444; }

/* header */
.base_header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 5.3rem;
  z-index: 3;
  background: #1e78d0; }

/* main */
.base_main {
  min-height: 510px;
  padding-top: 5rem; }

/* footer */
.base_footer {
  position: relative;
  width: 100%;
  z-index: 0;
  margin-top: 4.0rem;
  background: #eee;
  border-top: 1px solid #e4e4e4;
  text-align: center; }

/* floating */
.floating_menu {
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  background: #4b597c;
  font-size: 0;
  height: 5.5rem; }
